<template>
    <v-container grid-list-md >

      <v-layout row pb-2>
      <v-flex xs10 offset-xs1>
        <v-card class="card--flex-toolbar">
          <v-toolbar card prominent>
            <v-toolbar-title class="body-2">
              Skills and Experience
            </v-toolbar-title>
            <v-spacer></v-spacer>
          </v-toolbar>
          <v-card-text>
            <v-list two-line>
            <template v-for="(item, index) in skills">
               <v-list-tile
                 ripple
                 :key="index"
               >
                 <v-list-tile-content>
                   <v-list-tile-title><h4>{{ item.category }}</h4></v-list-tile-title>
                   <v-list-tile-subtitle>{{ item.contents }}</v-list-tile-subtitle>
                 </v-list-tile-content>
               </v-list-tile>
               <v-divider v-if="index != skills.length-1"></v-divider>
             </template>
           </v-list>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
      <v-layout row >
    <v-flex xs12>
      <v-card>
        <v-card-title>
          <h3>Additional</h3>
          <p>
            Have been responsible for the entire lifecycle of many projects from
            requirements, design, implementation, and test. Able to break down
             large amount of requirements into distinct functional areas.
             Strives to prioritize work that results in the most value to the client.
          </p>
          <p>Accustomed to assisting with regulatory compliance in various fields:
            casino gaming, gov't contracting, DOT compliance, FAA compliance,
             business documentation.
           </p>
        <p>
          I have written Django-based customer relations managements apps from the
          ground up. Companion Android and web-based apps allow receipt printing via Bluetooth
          and real time delivery and sales information. This includes invoicing,
          sales tracking, analysis, and asset management features. I also perform
          IT, consulting, and server administration as needed.
        </p>
        <p>I have used Node.js as an asynchronous messaging server for chat and process
          communication.</p>
          <p>I am experienced using HTTPS, SSL, especially using Let's Encrypt.</p>
        <p>I have used parallel clusters with thousands of nodes. I studied parallelization
          of serial algorithms for hydrodynamics. I've used genetic algorithms to find
          best fit parameters for various systems complete with simulations and visualization
          code.
         </p>
      </v-card-title>
      </v-card>
    </v-flex>
  </v-layout>
       <!-- <v-layout v-bind="binding">
         <v-flex >
           <v-card dark color="accent">
             <v-card-title>
               <div class="heading">Languages</div>
               <p>Python, JavaScript (ES5/ES6), Java</p>
             </v-card-title>
           </v-card>
         </v-flex>
         <v-flex>
           <v-card dark color="secondary">
             <v-card-text>two</v-card-text>
           </v-card>
         </v-flex>
         <v-flex>
           <v-card dark color="accent">
             <v-card-text>three</v-card-text>
           </v-card>
         </v-flex>
       </v-layout> -->
     </v-container>
</template>

<script>
export default {
  name: 'Introduction',
  data () {
    return {
      msg: '',
      skills: [
        { category: 'Languages', contents:	'Python, JavaScript (ES5/ES6), Java'},
        { category: 'JavaScript Frameworks', contents:	'	Vue, Vuetify, Pixi, Phaser, jQuery'},
        { category: 'Server Frameworks', contents:	'Django, Django REST Framework, Flask, Node.js'},
        { category: 'Databases', contents:	'MySQL, PostgreSQL, Oracle, SQLite'},
        { category: 'Computational Science',
          contents:	'Parallel computing, genetic optimization algorithms, numpy, BLAS, CUDA, MATLAB, Octave'},
        { category: 'PaaS', contents:	'AWS EC2, ELB, Lambda; Openshift'},
        { category: 'VCS', contents:	'git, github, gitlab clearcase'},
        { category: 'Miscellaneous', contents: "AJAX, multi-thread, multi-core, REST API, webpack" },
     ]
    }
  },
  computed: {
    binding () {
      // change to row on md breakpoint
      const binding = {}

      if (this.$vuetify.breakpoint.mdAndDown) binding.column = true

      return binding
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



